import React, { Component } from "react";
import { CCard, CCardBody } from "@coreui/react-pro";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
export default class SchoolDashboard extends Component {
  constructor(props) {
    super(props);
    console.log(props)
    let school_id = this.props.match.params.school_id;
    this.state = {
      school_id: school_id,
    };
  }
  render() {
    let { school_id } = this.state;
    return (
      <CCard>
        <CCardBody>
          <NavBar id={school_id} />
        </CCardBody>
      </CCard>
    );
  }
}
